:root {
  --dark-grey: #00000040;
  --grey: #f6f6f7;
  --none-outline: none;
  --bold-weight: bold;
  --border-radius: 3px;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
}

@font-face {
  font-family: Avenir-Heavy;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: bold;
}

html {
  box-sizing: border-box;
  font-weight: 300;
}

.form {
  flex-direction: column;
  margin-top: 20px;
  display: flex;
}

.form__input {
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  padding: 0;
  font-family: Avenir, Arial, sans-serif;
  box-shadow: 0 1px 8px #3d4e611a;
}

.form__input::placeholder {
  color: #3d4e61;
}

.input--big {
  background-position: 26px, 0;
  height: 68px;
  padding-left: 62px;
  font-size: 16px;
  line-height: 22px;
}

.input--big:hover {
  box-shadow: 0 4px 4px 0 var(--dark-grey);
}

.input--big:focus {
  box-shadow: 0 4px 4px 0 var(--dark-grey);
  border-radius: var(--border-radius);
  outline: var(--none-outline);
  font-weight: var(--bold-weight);
  background: linear-gradient(180deg, #fff 0%, var(--grey) 100%);
}

.input--small {
  background-position: 13px, 0;
  background-size: 11px, 11px;
  height: 40px;
  padding-left: 33px;
  font-size: 14px;
  line-height: 19px;
}

.input--small:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.input--small:focus {
  border-radius: var(--border-radius);
  outline: var(--none-outline);
  font-weight: var(--bold-weight);
  background: linear-gradient(180deg, #fff 0%, var(--grey) 100%);
}
